import React, { Component } from 'react';
import Select from 'react-select';
const $=window.$;
const jobObj={
  "cityLocation":"",
 "clientName":"",
 "creatorName":atob(localStorage.getItem("token")).split(":")[0].split("@")[0],
 "invitedUserEmails":[],
 "isArchived":false,
 "isInvited":false,
 "jobID":"",
 "maxExpense":"",
 "minExpense":"",
 "primarySkill":"",
 "subLocation":""
};
class AddJob extends Component {

    constructor(props){
        super(props);
        this.state={
         invitedUserEmails:[],
         jobObj:jobObj,
      };
      }



    componentWillReceiveProps(nextProps){
            
        // if(nextProps.candidate.candidateContact) {
            // this.setState({ candidate: nextProps.candidate });
        // }
    }
    

    
    addPrimarySkill(skill){
      let that=this;
      $.ajax({
        url:"http://52.230.124.2/addSkillWeb.php",
        type:"post",
        data:{
          skill:skill
        }       
      }).done(data=>{
        if(typeof data=="string")data=JSON.parse(data);
        console.log(data);
      })
      .fail((err,e,xhr)=>{
        console.log(xhr);
      });
    }


    addClientName(name){
      let that=this;
      $.ajax({
        url:"http://52.230.124.2/addClientWeb.php",
        type:"post",
        data:{
          client:name
          }      
      }).done(data=>{
        if(typeof data=="string")data=JSON.parse(data);
        console.log(data);
      })
      .fail((err,e,xhr)=>{
        console.log(xhr);
      });
    }

    generateJobId(){
      let chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZ-abcdefghijklmnopqrstuvwxyz0123456789";
      let sb = [];
      for (let i = 0; i < 30; i++) {
          let c = chars[Math.floor(Math.random()*30)];
          sb.push(c);
      }
      let random= sb.join("");
      console.log(random);
      return random;
    }
    

    componentDidMount(){

      let cityOptions={ 
        types: ['(cities)'],
        componentRestrictions:{"country":"in"}
        };
      let subLocationOptions={
        types:['(regions)'],
        componentRestrictions:{"country":"in"}
      }
        if(window.google)
      {  
        this.cityAutoComplete = new window.google.maps.places.Autocomplete(document.getElementById('jobloccity'),cityOptions);
        window.subLocationAutoComplete = new window.google.maps.places.Autocomplete(document.getElementById('jobloc'),subLocationOptions);
      }
    }


  render(){
    let jobObj=this.state.jobObj;
    return (<div>
          <div id="addJob_modal" className="modal fade" role="dialog" data-backdrop="static" tabIndex="4">
                <div className="modal-dialog modal-lg">
                  <div className="modal-content">
                      
                    <div className="modal-header">
                      <button type="button" className="close" data-dismiss="modal">&times;</button>
                        <h4 className="modal-title"><strong>Add Job</strong></h4>
                    </div>
                    <form className="form-horizontal" onSubmit={(e)=>{
                      e.preventDefault();
                      this.addJob(e)}}>

                    <div className="modal-body">
                   
                      <div className="form-group">
                                <label htmlFor="clientname" className="col-xs-2 control-label">Client Name</label>

                                <div className="col-xs-9">

                                  <Select
                                  id="clientname"  
                                  name="clientname"  
                                  value={jobObj.clientName} 
                                  onChange={this.onChangeClient.bind(this)} 
                                  placeholder="Client Name"
                                  required={true}
                                  options={this.props.clientNames}
                                  />
                                </div>
                      </div>

                      <div className="form-group">
                                <label htmlFor="primaryskill" className="col-xs-2 control-label">Primary Skill</label>
              
                                <div className="col-xs-9">
                                <Select
                                  id="primaryskill" 
                                  value={jobObj.primarySkill} 
                                  name="primarySkill"
                                  onChange={this.onChangePrimarySkill.bind(this)} 
                                  placeholder="Primary Skill"
                                  required={true}
                                  options={this.props.primarySkills}
                                  />
                                  </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="jobloc" className="col-xs-2 control-label">Job Location(Area)</label>
              
                                <div className="col-xs-9">
                                  <input type="text" className="form-control"  id="jobloc"
                                  name="jobLocation" placeholder="Job Location(Area)" 
                                  required/>
                                </div>
                      </div>
                      <div className="form-group">
                                <label htmlFor="jobloccity" className="col-xs-2 control-label">Job Location(City)</label>
              
                                <div className="col-xs-9">
                                  <input type="text" className="form-control"  id="jobloccity"
                                  name="jobLocationcity" placeholder="Job Location(City)" required/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="minexp" className="col-xs-2 control-label">Min. Experience</label>
              
                                <div className="col-xs-9">
                                  <input type="number" className="form-control"
                                  value={jobObj.minExpense} name="minexp"  min="0"
                                  onChange={(e)=>this.onChange(e,'minExpense')}  id="minexp" placeholder="Min. Experience" required/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="maxexp" className="col-xs-2 control-label">Max. Experience</label>
              
                                <div className="col-xs-9">
                                  <input type="number" className="form-control"
                                  value={jobObj.maxExpense} name="maxexp" min="0"
                                  onChange={(e)=>this.onChange(e,'maxExpense')}  id="maxexp" placeholder="Max. Experience" required/>
                                </div>
                      </div>     
                      <div className="form-group">
                               <label htmlFor="invites" className="col-xs-2 control-label">Invite</label>
              
                                <div className="col-xs-9 col-sm-6">
                                  <input type="email" className="form-control" name="invites" 
                                  id="invites" 
                                  // onChange={this.onChangeCtc.bind(this)} 
                                  placeholder="Invite Employers" list="userlist" autoComplete="off"/>
                                  <datalist id="userlist">
                                    { this.props.domainRegUsers.map((val,index)=>{
                                      return  <option value={val.value} key={index}/>
                                    })}
                                  </datalist>
                                </div>
                                <div className="col-xs-12 col-sm-3">
                                   <a className="btn btn-primary" onClick={this.invitedUserEmails.bind(this)}>ADD EMAIL</a> 
                                </div>
                      </div>
                      {this.state.invitedUserEmails.map((value, index)=> {
                          return (
                            <div className="form-group" key={index} style={{margin:"5px"}}>
                                 <div className="badge col-xs-10 text-left" style={{fontSize:14,display:'block',float:"none",margin:"auto",padding:8,backgroundColor:'blue'}}>{value}
                                 <a onClick={this.removeFromInvites.bind(this,value)} className="pull-right" style={{color:'white',padding:8,marginTop:-8,cursor:"pointer",fontSize:14}}><i className="fa fa-times"></i>  </a></div>
                          </div>
                          )
                      })}
                                    
                        </div>
                    
                    <div className="modal-footer">
                     <input type="submit" className="btn btn-primary" 
                    //  data-dismiss="modal" onClick={this.addJob.bind(this)} 
                     value="ADD"/>
                      <input type="reset" className="btn btn-default" data-dismiss="modal" value="Close"/>
                    </div>
                    </form>
                  </div>
              
                </div>
        </div>
      </div>
    );
  }

  addJob(e){
    e.preventDefault();
    if(this.props.clientNames.indexOf(this.state.jobObj.clientName)==-1)
    this.addClientName(this.state.jobObj.clientName);
    
    if(this.props.primarySkills.indexOf(this.state.jobObj.primarySkill)==-1)
    this.addClientName(this.state.jobObj.primarySkill);

    let jobid=this.generateJobId();
    this.state.jobObj.subLocation=$("#jobloc").val();
    this.state.jobObj.cityLocation=$("#jobloccity").val();
    this.state.jobObj.jobID=jobid;
    this.state.jobObj.invitedUserEmails=this.state.invitedUserEmails;
    if(this.state.invitedUserEmails.length)this.state.jobObj.isInvited=true;
    else this.state.jobObj.isInvited=false;
    this.state.jobObj.minExpense=parseFloat(this.state.jobObj.minExpense);
    this.state.jobObj.maxExpense=parseFloat(this.state.jobObj.maxExpense);
    console.log(this.state.jobObj);
    this.sendInvites(jobid);
    this.props.addJob(this.state.jobObj);
    $("#addJob_modal").modal("hide");
  }

  invitedUserEmails(){
    let val =$("#invites").val();
    let emails=JSON.parse(JSON.stringify(this.state.invitedUserEmails));
    if(emails.indexOf(val)==-1)
    emails.push(val);
    this.setState({
      invitedUserEmails:emails
    }) 
  $("#invites").val("");
    
  }

  removeFromInvites(val){
    let emails=JSON.parse(JSON.stringify(this.state.invitedUserEmails));
    let index=emails.indexOf(val);
    if(index>-1)
    { emails.splice(index,1);
    this.setState({
      invitedUserEmails:emails
    });
    }
  }

  sendInvites(jobid){
    let that=this;
    for(let x of this.state.invitedUserEmails){
      console.log(x);
      $.ajax({
        url:'http://52.230.124.2/getUserByEmailIdWeb.php',
        type:"post",
        data:{
          email:x
        }
      }).done((da)=>{
        da=JSON.parse(da);
        if(da.userId!="Invalid ID")
        $.ajax({
          url:'http://52.230.124.2/addInvitedJobsToUserWeb.php',
          type:"post",
          data:{
            userId:da.userId,
            jobId:jobid
          }
        })
        .done((data)=>{
          if(JSON.parse(data)["ok"]==1)
          console.log("invited",da.userId);
        }).fail((e,ee,xhr)=>{
          console.log(xhr);
        });
      }).fail((e,ee,xhr)=>{
        console.log(xhr);
      })
    }
  }


  onChangeCityLocation(e){
    let jobObj=JSON.parse(JSON.stringify(this.state.jobObj));
    jobObj.cityLocation= e.target.value;
    this.setState({
        jobObj
    });
  } 
  onChangeClient(e){
    console.log(e)
    let jobObj=JSON.parse(JSON.stringify(this.state.jobObj));
    jobObj.clientName= e?e.value:"";
    this.setState({
        jobObj
    });
  } 
  onChangePrimarySkill(e){
    let jobObj=JSON.parse(JSON.stringify(this.state.jobObj));
    jobObj.primarySkill=e?e.value:"";
    this.setState({
        jobObj
    });
  }
  onChangesubLocation(e){
    let jobObj=JSON.parse(JSON.stringify(this.state.jobObj));
    jobObj.subLocation = e.target.value;
    // console.log(e.target.value)
    this.setState({
        jobObj
    });
  }

  onChange(e,key){
    let jobObj=JSON.parse(JSON.stringify(this.state.jobObj));
    jobObj[key]= parseFloat(e.target.value)==NaN?0:e.target.value;
    this.setState({
        jobObj
    });
  }

}

export default AddJob;



