import React from  'react';
export default class NewColModal extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            columnName: ""
        };
    }
   
    onChangeName(event) {
        let newName =  event.target.value;
        this.setState({
            columnName: newName
        });
    }

    render(){
        return (
       
      <div>
      <div className="modal fade" id="newCol_modal" tabIndex="6" role="dialog" aria-labelledby="myModalLabel4" data-backdrop="static">
  <div className="modal-dialog" role="document">
    <div className="modal-content">
      <div className="modal-header">
        <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={this.reset.bind(this)}><span aria-hidden="true">&times;</span></button>
        <h4 className="modal-title" id="myModalLabel">Add Column</h4>
      </div>
      <div className="modal-body">
        
      <div className="form-group">
                <label htmlFor="col-name" className="col-sm-2 control-label">Column Name</label>
            
                 <div className="col-sm-9">
                        <input type="text" className="form-control" name="name"
                        value={this.state.columnName} id="col-name" onChange={this.onChangeName.bind(this)} placeholder="Column Name"/>
                    </div>
        </div>
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-default" data-dismiss="modal" onClick={this.reset.bind(this)}>Close</button>
        <button type="button" className="btn btn-success"data-dismiss="modal" onClick={this.props.addColumn.bind(null,this.state.columnName)}>Add</button>
      </div>
    </div>
  </div>
</div>
      </div>
  );
    }

    reset(){
        this.setState({
            columnName:""
        });
    }

}



