const feedbackMapper=(primaryStaus,secondaryStatus,subSecStatus,timestamp)=>{
    let feedbackReson="",feedback="";
    primaryStaus=primaryStaus.toLowerCase();
    secondaryStatus=secondaryStatus.toLowerCase();
    timestamp=parseInt(timestamp)||0;

    switch (primaryStaus){
        case "new":
            feedbackReson="Introductory Call";
            if(secondaryStatus==="not interested")
                    feedback=subSecStatus;
            else if(secondaryStatus==="interested")
                    feedback="Candidate is interested";
            else if(secondaryStatus==="interested but unavailable")
                    feedback=`Candidate requested reschedule to ${timestamp}`;
            break;
        
        case "screening":
            feedbackReson="Shortlist Confirmation Call";
            if(secondaryStatus==="not interested")
                    feedback=subSecStatus;
            else if(secondaryStatus==="interested")
                    feedback="Candidate is interested";
            else if(secondaryStatus==="interested but unavailable")
                    feedback=`Candidate requested reschedule to ${timestamp}`;
            else if(secondaryStatus==="screen select")
                    feedback="Screen Select";                  
            else if(secondaryStatus==="screen reject")
                    feedback="Asked to call later"; 
            else if(secondaryStatus==="sent - screening")
                    feedback="Candidate is interested"; 
            break;
        
        case "ipending":
            feedbackReson="Interview Confirmation Call";
            if(secondaryStatus==="interview confirmed")
                    feedback=`Candidate confirmed availability at ${timestamp}`+":"+subSecStatus;
            else if(secondaryStatus==="reschedule by candidate")
                    feedback=`Candidate requested reschedule to ${timestamp}`;
            else if(secondaryStatus==="not interested")
                    feedback=subSecStatus;
            else if(secondaryStatus==="pending")
                    feedback=`Asked to call at ${timestamp}`+":"+subSecStatus;           
            else if(secondaryStatus==="no show"){
                feedback="No Show"+":"+subSecStatus;      
                feedbackReson="Post Interview Call";
            }
            break;
        
        case "interviewed":
            
            feedbackReson="Post Interview Call";
            if(secondaryStatus==="not interested")
                    feedback=subSecStatus;
            else if(secondaryStatus==="interested")
                    feedback="Candidate is interested";
            else if(secondaryStatus==="reschedule by candidate")
                    feedback=`Candidate requested reschedule to ${timestamp}`;
            else if(secondaryStatus==="interview select")
                    feedback=subSecStatus;                  
            else if(secondaryStatus==="interview reject")
                    feedback=subSecStatus;                     
            break;
        
        case "offered":
            
            feedbackReson="Offer Call";
            if(secondaryStatus==="not interested")
                 feedback=subSecStatus;
            else if(secondaryStatus==="offer discussion")
                feedback="Offer Discussion";
            else if(secondaryStatus==="offered")
                feedback=`Offered`;
            else if(secondaryStatus==="candidate interested with offer")
                 feedback=`Candidate is Interested with offer`;
                 //jhhhhhhhhhhhhh
            else if(secondaryStatus==="ditched")
            {   feedbackReson='Post Offer Call';
                feedback=`Candidate ditched with offer`;}
            break;
    
        case "joined":
            feedbackReson="Post Offer Call";
            if(secondaryStatus==="joined")
                feedback="Candidate Joined";
            else if(secondaryStatus==="joined & left")
                feedback="Candidate Joined and Left";
            break;
    
    }

    let feedbackObj={
        feedbackReason:feedbackReson,
        feedback:feedback.split(":")
    }
    
    return feedbackObj;
}

export default feedbackMapper;


