searchBy(val){
    let list=[];
    if(val!=="")
    list=this.state.candidateList.filter(function(obj){
      return obj.candidateEmail.toLowerCase().includes(val)||
             obj.candidateName.toLowerCase().includes(val)||
             obj.candidateContact.toLowerCase().includes(val)||
             obj.work_experience[0].organization.toLowerCase().includes(val)||
             obj.work_experience[0].role.toLowerCase().includes(val)||
             obj.experience.toLowerCase().includes(val)||
             parseFloat(obj.current_ctc)==val||
             parseInt(obj.noticePeriod)==val||
             obj.location.toLowerCase().includes(val)||
             obj.degree.toLowerCase().includes(val)||
             obj.college.toLowerCase().includes(val);
    });
    else list=this.state.JobWiseObj[this.jobFilter];
    this.setState({candidateList:list});
}
  
