filterByJob(val){
    if(!val)return;

    let refList=this.state.refObjKeys;
    let newKeys=[];
    // this.state.newKeys.slice();
    let colSelected=JSON.parse(JSON.stringify(this.state.selectedColRef));
   
    let list=this.state.JobWiseObj[val];
    list.forEach((obj,index)=>{

      let nowKeys=Object.keys(obj);
      if(refList.length<nowKeys.length)
      {
        for(let x of nowKeys){
          if(refList.indexOf(x)==-1&&newKeys.indexOf(x)==-1){
            newKeys.push(x);
            colSelected[x]=true;
            // console.log(colSelected[x])
          }
        }
      }
    });
    // console.log(newKeys,colSelected)
    this.jobFilter=val;
    this.setState({
      candidateList:list,
      selectedCol:colSelected,
      newKeys:newKeys});
}

filterByStatus(val){
        // console.log(val);
        if(val)
        {
        let list=this.state.JobWiseObj[this.jobFilter];
        let valLength = val.length;

        list=list.filter((obj,index)=>{
          let addedByObj=obj.added_by.find((obj)=>{
            if(obj)
            if(this.findFinalStatus(obj.status).length>=val.length &&
              obj.primarySkill.trim()+' - '+obj.clientName.trim()==this.jobFilter)
            if(this.findFinalStatus(obj.status).substring(0,val.length)==val)
              return true;
          });
            if(addedByObj)
            if(this.findFinalStatus(addedByObj.status).substring(0,val.length)==val)
            return true;
          }
        );
        // console.log(list);
        this.statusFilter=val;
        this.setState({candidateList:list});
    }
}

findFinalStatus(statusList){
  let finalStatus = statusList[statusList.length - 1].status;
  finalStatus=finalStatus.toString();
  if((finalStatus===""||finalStatus == null||finalStatus=="NaN"||finalStatus.includes("null"))
    && statusList.length>1){
      finalStatus = statusList[statusList.length - 2].status;
  }
  // console.log(finalStatus);
  return finalStatus;
}

// now filtering by first name of recruiter
filterByReruiter(val){
    if(!val)return;
 
    let list=this.state.JobWiseObj[this.jobFilter];
    list=list.filter((obj,index)=>{
      let found=false;
      obj.added_by.forEach((obj)=>{
        if(obj)
        if(obj.userName===val&& obj.primarySkill.trim()+' - '+obj.clientName.trim()==this.jobFilter)
        {found=true;}
      });
      return found;
    });
    this.recuterFilter=val;
    this.setState({candidateList:list});
}

