import React, { Component } from 'react';
import swal from 'sweetalert2';
const $=window.$;
const sample={
    added_by:[
    { Timestamp:new Date().getMilliseconds(),
      clientName:"",
      companyURL:"",
      domain:atob(localStorage.getItem("token")).split(":")[0].split("@")[1],
      jobID:"",
      position:0,
      primarySkill:"",
      userEmail:atob(localStorage.getItem("token")).split(":")[0],
      userID:localStorage.getItem("id"),
      userName:localStorage.getItem("name"), 
      status:[
        {status:"new:New Resume",
        Timestamp:"",
        userID:localStorage.getItem("id")}
      ]
    }],
    candidateContact:"",
    candidateEmail:"",
    candidateName:"",
    work_experience:[{
        organization:"",
        role:""
    }],
    current_ctc:"",
    noticePeriod:"",
    degree:"",
    college:"",
    'et/ec':0,
    experience:"",
    ctcFixed:0,
    location:"",
    "preferred_location": "",
    "current_role":"",
    "designation":"",
    "employer":"",
    "previous_role":"",
    "highest_degree":"",
    "notice_period":"",
    "key_skills":"",
    "may_also_know": "",
    "phone_no":"",
    "email_id":"",
    "work_summary":{
      "description": "",
      "industry": "",
      "functional_area": "",
      "role": ""
  },
  "work_experience": [
      {
          "role":"",
          "organization": "",
          "skills": "  ",
          "duration": ""
      }
  ],
  "education": [],
  "it_skills": [],
  "other_details": {
    "languages_known": [],
    "personal_details": {
        "date_of_birth": "",
        "gender": "",
        "marrital_status": "",
        "category": "",
        "physically_challenged": "",
        "address": " ",
        "contact": "",
        "email": ""
    },
    "desired_job_details": {
        "job_type": "",
        "employment_status": ""
    },
    "work_authorization": {
        "us_work_status": "",
        "other_country_status": ""
    }
},
};
class AddCandidateModal extends Component {

    constructor(props){
        super(props);
        this.state={
         candidate:sample,
         designationsList:[],
         collegeList:[]
        };

        this.reset=this.reset.bind(this);
    }

    reset(){
        this.setState({candidate:sample});
    }

    

    onChangeCompany(event){
        
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.work_experience[0].organization=newCandidate.employer=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeDesignation(event){
        
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.work_experience[0].role=newCandidate.designation=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeCtc(event){

        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.current_ctc=event.target.value;
        newCandidate.ctcFixed=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeNotice(event){
        
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.noticePeriod=event.target.value;
        newCandidate.notice_period=parseInt(event.target.value/60)+" Month";
        this.setState({
            candidate:newCandidate
        });
    }

  
    change(event, value) {
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate[value] = event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }


  render(){
    let candidate=this.state.candidate;
    return (
      <div>
          
          <div id="add_modal" className="modal fade" role="dialog" data-backdrop="static" tabIndex="2">
                <div className="modal-dialog modal-lg">
                  <div className="modal-content">
                      
                    <div className="modal-header">
                      <button type="button" className="close" data-dismiss="modal" onClick={this.reset}>&times;</button>
                        <h4 className="modal-title"><strong>Add Candidate</strong></h4>
                    </div>
                    
                    <form className="form-horizontal" onSubmit={(e)=>{
                       e.preventDefault();
                       this.addCandidate();
                       $("#add_modal").modal('hide');}}>
                    <div className="modal-body">
                     
                      {/* <div className="form-group">
                                <label htmlFor="stat1" className="col-sm-2 control-label">Status</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="status" onChange={()=>this.change(this,"")} 
                                   id="stat1" placeholder="Status"/>
                                </div>
                      </div> */}
                      
                      <div className="form-group">
                                <label htmlFor="course_name1" className="col-sm-2 control-label"><sup style={{color:"red"}}>* </sup>Name</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="candidateName"  required
                                  value={candidate.candidateName} id="course_name1" onChange={(event)=>this.change(event,"candidateName")} placeholder="candidate Name"/>
                                </div>
                      </div>
                      <div className="form-group">
                                <label htmlFor="mail" className="col-sm-2 control-label"><sup style={{color:"red"}}>* </sup>Mail Id</label>
              
                                <div className="col-sm-9">
                                  <input type="email" className="form-control"  id="mail" required
                                  value={candidate.candidateEmail} name="candidateEmail" onChange={(event)=>this.change(event,"candidateEmail")} placeholder="Mail Id"/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="mob" className="col-sm-2 control-label"><sup style={{color:"red"}}>* </sup> Mobile Number</label>
              
                                <div className="col-sm-9">
                                  <input type="tel" className="form-control"  id="mob" required
                                  value={candidate.candidateContact} name="candidateContact" onChange={(event)=>this.change(event,"candidateContact")} placeholder="Mobile Number"/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="comp" className="col-sm-2 control-label"><sup style={{color:"red"}}>* </sup> Company</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control"  id="comp" required
                                  value={candidate.work_experience[0].organization}
                                  onChange={this.onChangeCompany.bind(this)} name="company" placeholder="Company"/>
                                </div>

                      </div>     
                      <div className="form-group">
                                <label htmlFor="desg" className="col-sm-2 control-label"><sup style={{color:"red"}}>* </sup> Designation</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control"  required
                                  value={candidate.work_experience[0].role} name="current_role" 
                                  onChange={this.onChangeDesignation.bind(this)}  id="desg" placeholder="Designation" list="desg-list"/>
                                </div>
                                <datalist id="desg-list">
                                  {this.state.designationsList.map((obj,index)=><option key={index}  value={obj}/>)}
                                  </datalist>  
                      </div>     
                      <div className="form-group">
                                <label htmlFor="exp" className="col-sm-2 control-label">Experience</label>
              
                                <div className="col-sm-9">
                                  <input type="number" className="form-control" name="experience" min="0"
                                  value={candidate.experience} id="exp" 
                                  onChange={(event)=>this.change(event,"experience")} placeholder="Experience"/>
                                </div>
                      </div>     

                      <div className="form-group">
                                <label htmlFor="ctc" className="col-sm-2 control-label">CTC</label>
              
                                <div className="col-sm-9">
                                  <input type="number" className="form-control" name="current_ctc" min="0"   
                                  value={candidate.current_ctc} id="ctc" onChange={this.onChangeCtc.bind(this)} placeholder="CTC"/>
                                </div>
                      </div>

                      <div className="form-group">
                                <label htmlFor="et" className="col-sm-2 control-label">ET/EC</label>
              
                                <div className="col-sm-9">
                                  <input type="number" className="form-control" name="et" min="0" 
                                  value={candidate["et/ec"]} id="et" onChange={(event)=>this.change(event,"et/ec")} placeholder="ET/EC"/>
                                </div>
                      </div>  

                      <div className="form-group">
                                <label htmlFor="np" className="col-sm-2 control-label">Notice Period</label>
              
                                <div className="col-sm-9">
                                  <input type="number" className="form-control" name="noticePeriod" 
                                  value={candidate.noticePeriod} id="np" min="0"
                                  onChange={this.onChangeNotice.bind(this)} placeholder="Notice Period - (in days)"/>
                                </div>
                      </div> 

                      <div className="form-group">
                                <label htmlFor="univ" className="col-sm-2 control-label"><sup style={{color:"red"}}>* </sup> University</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="college"  required
                                  value={candidate.college} id="univ" onChange={(event)=>this.change(event,"college")} placeholder="University"
                                  list="collg-list"/>
                                </div>
                                
                                <datalist id="collg-list">
                                  {this.state.collegeList.map((obj,index)=><option key={index}  value={obj}/>)}
                                  </datalist>
                      </div>      
                      <div className="form-group">
                                <label htmlFor="degree" className="col-sm-2 control-label"><sup style={{color:"red"}}>* </sup>Degree</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="degree"  required
                                  value={candidate.degree} id="degree" onChange={(event)=>this.change(event,"degree")} placeholder="Degree"/>
                                </div>
                      </div>
                      <div className="form-group">
                                <label htmlFor="location" className="col-sm-2 control-label"><sup style={{color:"red"}}>* </sup>Location</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" id="location" required
                                  value={candidate.location} name="location" onChange={(event)=>this.change(event,"location")} placeholder="Location"/>
                                </div>
                      </div>

                      <div className="form-group">
                                <label htmlFor="primary-skill" className="col-sm-2 control-label">Primary Skill</label>
              
                                <div className="col-sm-9">
                                <select name="" id="primary-skill" className="form-control">
                                {this.props.JobInfoList.map((obj,index)=>
                                  <option value={JSON.stringify(obj)} key={index}>{obj.primarySkill+' - '+obj.clientName}</option>
                                )}
                                </select>
                                </div>
                      </div>

                           
                        </div>
                    
                    <div className="modal-footer">
                     <input type="submit" className="btn btn-primary" value="SAVE"/>
                      <input type="reset" className="btn btn-default" data-dismiss="modal" onClick={this.reset} value="Close"/>
                    </div>
                </form>
                  </div>
              
                </div>
                       
        </div>
      </div>
    );
  }

  addCandidate(){
    let candidate=this.state.candidate;
    candidate.added_by[0].Timestamp=Date.now()
    candidate.added_by[0].status[0].Timestamp=Date.now();
    let obj =JSON.parse(document.getElementById("primary-skill").value);
    candidate.added_by[0].domain=obj.domain;
    candidate.added_by[0].clientName=obj.clientName;
    candidate.added_by[0].primarySkill=obj.primarySkill;
    candidate.added_by[0].jobID=obj.jobID;
    candidate.other_details.personal_details.email=candidate.email_id=candidate.candidateEmail;
    candidate.other_details.personal_details.contact=candidate.phone_no=candidate.candidateContact;
    candidate["et/ec"]=parseFloat(candidate["et/ec"]).toFixed(1);
    candidate.experience=parseFloat(candidate.experience).toFixed(1);
    candidate.current_ctc=candidate.ctcFixed=parseFloat(candidate.current_ctc).toFixed(1);
    candidate.noticePeriod=candidate.notice_period=parseFloat(candidate.notice_period).toFixed(1);
    console.log(candidate);
    this.props.addCandidate(candidate);
    this.reset()
  }


  componentDidMount(){
    $('#add_modal').on('shown.bs.modal',()=>{
      if(!this.state.collegeList.length)
    $.get("http://52.230.124.2/getCollegeNames.php",(data)=>{
      // console.log(JSON.parse(JSON.stringify(data.data)));
      // console.log(JSON.parse(data).data);
      let list =JSON.parse(data).data,filteredList=[];

      for(let t of list)
      if(filteredList.indexOf(t)===-1){
        filteredList.push(t);
      }
      console.log(filteredList.length,list.length);
      this.setState({collegeList:filteredList});
    });
    
    if(!this.state.designationsList.length)
    $.get("http://52.230.124.2/getDesignations.php",(data)=>{
      // console.log(JSON.parse(JSON.stringify(data.data)));
      let list =JSON.parse(data).data,filteredList=[];
      for(let t of list)
      if(filteredList.indexOf(t)===-1){
        filteredList.push(t);
      }
      console.log(filteredList.length,list.length);
      this.setState({designationsList:filteredList});
    });
  });
  }

}

export default AddCandidateModal;



