import React, { Component } from 'react';
import StatusMapper from './statusMapper.js';
import FeedbackMapper from './FeedbackMapper';
import swal from 'sweetalert2';
import './../assets/bootstrap-datetimepicker.min.css';
var DateTimeField = require('react-bootstrap-datetimepicker');
var $=window.$;
var currentPrimaryStatus="",oldPrimaryStatus="";
class StatusUpdateModal extends Component {

  constructor(props){
    super(props);
    this.state={
      candidate:this.props.currentCandidate,
      statusArray:StatusMapper["new"],
      subStatusArray:[],
      subSubStatusArray:[] ,
      currentStatusList:[]
    };
    this.time=0;
  }

  componentWillReceiveProps(nextProps){
    // console.log(this.state.candidate)
    // console.log(document.getElementById("jobs").value);
    // console.log(nextProps.currentCandidate.added_by);
  if(nextProps.currentCandidate.candidateContact.length){
    let statusObj=nextProps.currentCandidate.added_by.find((obj)=>{
      if(obj)
        if( (obj.primarySkill.trim()+' - '+obj.clientName.trim())  == document.getElementById("jobs").value)
        return true;
    })||nextProps.currentCandidate.added_by[0];
    console.log(statusObj);
    currentPrimaryStatus=oldPrimaryStatus=this.findFinalStatus(statusObj.status).split(":")[0];
    // console.log(status,StatusMapper[status]);
    this.setState({ 
      candidate:nextProps.currentCandidate,
      statusArray:StatusMapper[currentPrimaryStatus],
      subStatusArray:[],
      subSubStatusArray:[],
      currentStatusList:statusObj.status 
    });
    this.time=0;
  }
}
    
  findFinalStatus(statusList){
    let finalStatus = statusList[statusList.length - 1].status;
    finalStatus=finalStatus.toString();
    if((finalStatus===""||finalStatus == null||finalStatus=="NaN"||finalStatus.includes("null"))
      && statusList.length>1){
        finalStatus = statusList[statusList.length - 2].status;
    }
    // console.log(finalStatus);
    return finalStatus;
  }

  render() {
      
    return (
      <div>
      <div className="modal fade" id="status_modal" tabIndex="-10" role="dialog" aria-labelledby="myModalLabel4" data-backdrop="static">
  <div className="modal-dialog" role="document">
    <div className="modal-content">
      <div className="modal-header">
        <button type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
        <h4 className="modal-title" id="myModalLabel">-- Select Status --</h4>
      </div>
      <div className="modal-body">
      <div className="text-center"> 
      <h3><strong>{this.props.currentCandidate.candidateName+'  -  '}</strong>
        <strong style={{color:"#40C4F4"}}>{this.getDescPrimarySatus()}</strong>
      </h3>
      </div>
        <div className="text-center">
        <div className="col-xs-12 col-sm-10" style={{float:"none",margin:"auto"}}>

        <select name="" id="sub-status" className="form-control" 
        style={{margin:"10px auto"}}
        onChange={this.onChangeSecStatus.bind(this)}>
        
        <option value=""> -- Select Status -- </option>
        {this.state.statusArray.map((obj,index)=>
        <option value={obj.sec_status} key={index}>{obj.sec_status}</option> 
        )} 
        </select>
        {
          this.state.subStatusArray.length
          ?
           (this.state.subStatusArray.length==1 
           ?
          (<div>
            <h4>{this.state.subStatusArray[0].key}</h4>

          {this.state.subStatusArray[0].sub_sub_status.map((obj,index)=>{
            if(obj.type=="number")
             { return (
             <select name={obj.name} id="aa" className="form-control"
             style={{margin:"10px auto"}} key={index}>
             <option value="">-- Select {obj.name.indexOf("_")==-1?obj.name:obj.name.replace("_","")} --</option>
             {obj.options.map((val,index)=>{
               return <option value={val} key={index}>{val}</option>}
            )}
             </select>)
             }
             else 
             if(obj.type=="datetime")
             return (
               <div className="dp" style={{margin:"10px auto"}} key={index}>
               <DateTimeField  defaultText="-- Select Date and Time --" onChange={(time)=>{this.time=time;}}/>
               </div>
             )
             
          })}
          </div>)
          :
          (
            <div>
          <div>
            <select name="" id="sec-sub-status" className="form-control" 
            onChange={this.onChangeSubSubStatus.bind(this)}
            style={{margin:"10px auto"}}>
            <option value=""> -- Select option -- </option>
            {
            this.state.subStatusArray.map((obj,index)=>
              <option value={obj.key} key={index}>{obj.key}</option> 
            )}  
            </select>
          </div>
          <div>
          { this.state.subSubStatusArray.length?
            this.state.subSubStatusArray.map((obj,index)=>{
              // console.log(obj)
              if(obj.type=="number")
               return (
                <select name={obj.name} id="aa" className="form-control"
                style={{margin:"10px auto"}} key={index}>
                <option value="">-- Select Round --</option>
                {obj.options.map((val,index)=>{
                  return <option value={val} key={index}>{val}</option>}
               )}
                </select>)
  
               else 
               if(obj.type=="datetime")
               return (<div className="dp" style={{margin:"10px auto"}} key={index}>
               <DateTimeField  defaultText="-- Select Date and Time --" onChange={(time)=>{this.time=time;}}/>
               </div>)
               
               
               else if(obj.type=="text")
               return <input type="text" name={obj.name} id="" 
               placeholder={obj.name.indexOf("_")==-1?obj.name:obj.name.replace("_","")}
               key={index} style={{margin:"10px auto"}} className="form-control"
               />
            })
            :null
          }
          </div>
          </div>)
          )
          :null
        }

        
      </div>
      </div>
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-default" data-dismiss="modal">Cancel</button>
        <button type="button" className="btn btn-success" data-dismiss="modal" onClick={this.addStatus.bind(this)}>Save</button>
      </div>
    </div>
  </div>
</div>
      </div>
    );
  }

  getDescPrimarySatus(){
    let obj={
      "new":"New Resume",
      "screening":"Screening",
      "ipending":"Interview Pending",
      "interviewed":"Interviewed",
      "offered":"Offered",
      "joined":"Joined"
    };

    return obj[currentPrimaryStatus];
  }

  onChangeSecStatus(e){
    let subStatusArray=[]
    if(e.target.value!=""){
      // console.log(this.state.statusArray)
        subStatusArray=this.state.statusArray.find((obj)=>{
          // console.log(obj)
          return obj.sec_status==e.target.value;
        })
        .sub_status;
    }
    // console.log(subStatusArray)
      this.setState({
        subStatusArray:subStatusArray
      });
  }

componentDidUpdate(){
    // console.log($(".dp .input-group.date.form-group-lg"))
    $(".dp .input-group.date.form-group-lg").removeClass("form-group-lg");
    $(".dp .input-group.date input[type=text]").attr("readonly","readonly");
    $(".dp .input-group.date input[type=text]").on('focus',function(){
      // console.log("event fire")
      $(".dp .input-group.date span")[0].click();
    });
    $('#status_modal').on('hidden.bs.modal',(e)=>{
      $("#sub-status").val("");
      this.setState({
        subStatusArray:[],
        subSubStatusArray:[]
      });          
    })
  }
componentWillUpdate(){
    $(".dp .input-group.date input[type=text]")
    .off( "focus")
  }

onChangeSubSubStatus(e){
    let subSubStatusArray=[];
    if(e.target.value!=""){
        subSubStatusArray=this.state.subStatusArray.find((obj)=>{
          return obj.key==e.target.value;
        })
        .sub_sub_status;
    }
    // console.log(subSubStatusArray)
      this.setState({
        subSubStatusArray:subSubStatusArray
      });

}


addStatus(){
    let status="";
    // console.log(this.state.currentStatusList);
    let secFilter=document.getElementById("sub-status").value;
  
    if(secFilter)
    {  
      if(secFilter.toLowerCase()=="next interview")secFilter="Pending";
      
      this.upgradeInPrimaryStatus(secFilter);
      // if(secFilter.toLowerCase()=="interview confirmed")
      // status=currentPrimaryStatus;
      // else 
      status=currentPrimaryStatus+":"+secFilter;

    if(this.state.subStatusArray.length==1){
      this.state.subStatusArray[0].sub_sub_status.forEach(element =>{
        if(element.type=="datetime"){
          {  
          if(this.time&&(! (new Date( parseInt(this.time))=="Invalid Date")) )
          status+=":"+this.time;
         }
        }
        else if(element.type=="number")
        status+=":"+window.$("#aa").val();
      });
    }
    else if(this.state.subStatusArray.length>1){
      status+=":"+document.getElementById("sec-sub-status").value;
      alert(status);
      if(this.state.subSubStatusArray.length>0&&
          this.state.subSubStatusArray.length==1){
            this.state.subSubStatusArray.forEach((obj)=>{
              if(obj.type=="text")status+=":"+window.$("input[name="+this.state.subSubStatusArray[0].name+"]").val();
              else 
              if(obj.type=="number")status+=":"+window.$("#aa").val(); 
            })
          }
    }

    let feedbackObj=FeedbackMapper(...status.split(":"),this.time,);

    if(secFilter.toLowerCase()=="interview confirmed")
      status=status.replace("Interview Confirmed","Pending");
      

    // alert("new status  -- "+status+"\n old status -- "+this.findFinalStatus(this.state.currentStatusList)
    // +' \n feedbackResson -- '+feedbackObj.feedbackReason +'\n feedback (array):'+feedbackObj.feedback.toString());
    
    this.props.updateStatus(status,feedbackObj.feedback,feedbackObj.feedbackReason);
  }
  else
  {
    swal("Cannot Add","Please Select Secondary Filter To Add Status","info");
  }
  }

  upgradeInPrimaryStatus(secSatus){
    if(currentPrimaryStatus==="new"&&secSatus=="Sent - Screening")
        currentPrimaryStatus="screening";
    else if(currentPrimaryStatus==="new"&&secSatus=="Interview Confirmed")
        currentPrimaryStatus="ipending";
    else if(currentPrimaryStatus==="screening"&&secSatus=="Interview Confirmed")
        currentPrimaryStatus="ipending";
    else if(currentPrimaryStatus==="ipending"&&secSatus=="Interview Select")
        currentPrimaryStatus="interviewed";
    else if(currentPrimaryStatus==="ipending"&&secSatus=="Interview Reject")
        currentPrimaryStatus="interviewed";
    else if(currentPrimaryStatus==="interviewed"&&secSatus=="Pending")
        currentPrimaryStatus="ipending";
    else if(currentPrimaryStatus==="interviewed"&&secSatus=="Candidate Accepted - Offer")
            currentPrimaryStatus="offered";
    else if(currentPrimaryStatus==="interviewed"&&secSatus=="Offered")
        currentPrimaryStatus="offered";
    else if(currentPrimaryStatus==="offered"&&secSatus=="Joined")
        currentPrimaryStatus="joined";
  }
}


export default StatusUpdateModal;



