import React, { Component } from 'react';

class ExcelTable extends Component {
  
  render(){
   let keys=this.props.colSelected;   
    return (
      <div 
      style={{"display":"none"}}
      >
        <table className="table" id="excel-table">

        <TableHeader colSelected={this.props.colSelected} newKeys={this.props.newKeys}/>
        
        <tbody>
        {this.props.list.map((value,index)=>
                                  { 
                                      if(value.selected){ 
                                      let obj=value.added_by.find((obj)=>{
                                        if(obj)
                                         if(obj.primarySkill.trim()+' - '+obj.clientName.trim()===document.getElementById("jobs").value)
                                         return true;
                                       });
                                      console.log(obj) 
                                    let statusList = obj.status;
                                       let finalStatus = statusList[statusList.length - 1].status,
                                       date=statusList[statusList.length-1].Timestamp;
                                       finalStatus=finalStatus.toString();
                                       if((finalStatus===""||finalStatus == null||finalStatus.includes("null")||finalStatus=="NaN")
                                   && statusList.length>1){
                                           finalStatus = statusList[statusList.length - 2].status;
                                           date=statusList[statusList.length-2].Timestamp;
                                       }
                                       finalStatus=finalStatus.split(":");
                                     
                                      return (
                                  <tr key={index}>
                                      {/* {return this.props.colSelected["Status"] */}
                                      {keys["Status"]?<td>{finalStatus[0]+" : "+finalStatus[1]}</td>:null}
                                      {keys["Last Action"]?<td>{new Date(parseInt(date)).toLocaleDateString()}</td>:null}
                                      {keys["Name"]?<td>{value.candidateName}</td>:null}
                                      {keys["Mail Id"]?<td>{value.candidateEmail}</td>:null}
                                      {keys["Mobile"]?<td>{value.candidateContact}</td>:null}
                                      {keys["Company"]?<td>{value.employer}</td>:null}
                                      {keys["Designation"]?<td>{value.designation}</td>:null}
                                      {keys["Experience"]?<td>{value.experience}</td>:null}
                                      {keys["CTC"]?<td>{value.current_ctc}</td>:null}
                                      {keys["et/ec"]?<td>{value["et/ec"]}</td>:null}
                                      {keys["Notice Period"]?<td>{value.noticePeriod}</td>:null}
                                      {keys["Univesity"]?<td>{value.college}</td>:null}
                                      {keys["Degree"]?<td>{value.degree}</td>:null}
                                      {keys["Location"]?<td>{value.location}</td>:null}
                                      {this.props.newKeys.map((val,index)=>{
                                          console.log("new keys in excel val",value[val]);
                                        return value[val]?<td key={index}>{value[val]}</td>:<td></td>;   
                                      })}
                                  </tr>
                                      );
                                    }
                                    
                                      else 
                                      return null;
                                  })
                                  }
                              </tbody>
        <TableFooter colSelected={this.props.colSelected} newKeys={this.props.newKeys}/>
        </table>
      </div>
    );
  }
}

export default ExcelTable;


const TableFooter= (props)=>{
  let obj=props.colSelected,headers=[],newKeys=props.newKeys;
    for(let x in obj){
        if(obj[x]===true){
            headers.push(x);
        }
    }
    for(let x in newKeys){
        if(obj[x]===true)
         headers.push(x);
    }

    return (
        <tfoot>
        <tr>  
          {headers.map((value,index)=>{
            return <th key={index}>{value}</th>
          })}
        </tr>
        </tfoot>
    );
}
const TableHeader =(props)=>{
  let obj=props.colSelected,headers=[],newKeys=props.newKeys;
    for(let x in obj){
        if(obj[x]===true){
            headers.push(x);
        }
    }
    for(let x in newKeys){
        if(obj[x]===true)
         headers.push(x);
    }
    return (
        <thead>
        <tr>  
          {headers.map((value,index)=>{
            return <th key={index}>{value}</th>
          })}
        </tr>
        </thead>
    );
}


