import React, { Component } from 'react';


class CandidateModal extends Component {

    constructor(props){
        super(props);
        this.state={
         candidate:this.props.candidate
        };
        }

    componentWillReceiveProps(nextProps){
            
        if(nextProps.candidate.candidateContact) {
            this.setState({ candidate: nextProps.candidate });
        }
    }

    onChangeName(event){
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.candidateName=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeEt(event){
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate["et/ec"]=event.target.value;
        this.setState({
            candidate:newCandidate
        });

    }

    onChangeCompany(event){
        
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.work_experience[0].organization=
        newCandidate.employer=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeDesignation(event){
        
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.work_experience[0].role=
        newCandidate.designation=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeExperience(event){

        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.experience=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeCtc(event){

        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.current_ctc=event.target.value;
        newCandidate.ctcFixed=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeNotice(event){
        
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.noticePeriod=event.target.value;
        newCandidate.notice_period=parseInt(event.target.value/60)+" Month";
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeUniversity(event){

        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.college=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeDegree(event){

        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.degree=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }
    onChangeLocation(event){

        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate.location=event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }

    change(event, value) {
        let newCandidate=JSON.parse(JSON.stringify(this.state.candidate));
        newCandidate[value] = event.target.value;
        this.setState({
            candidate:newCandidate
        });
    }


  render(){
    let candidate=this.state.candidate;
    let statusList = candidate.added_by[0].status;
    let finalStatus = statusList[statusList.length - 1].status,
    date=statusList[statusList.length-1].Timestamp;
    if((finalStatus===""||finalStatus == null||finalStatus.includes("null"))
         && statusList.length>1){
        finalStatus = statusList[statusList.length - 2].status;
        date=statusList[statusList.length-2].Timestamp;
    }
    finalStatus=finalStatus.split(":")[finalStatus.split(":").length-1]
    
    // ==null?this.state.candidate:this.props.candidate;
    return (
      <div>
          
          <div id="course_modal" className="modal fade" role="dialog" data-backdrop="static" tabIndex="5">
                <div className="modal-dialog modal-lg">
                  <div className="modal-content">
                      
                    <div className="modal-header">
                      <button type="button" className="close" data-dismiss="modal">&times;</button>
                        <h4 className="modal-title"><strong>Candidate</strong></h4>
                    </div>
                    <div className="modal-body">
                    <form className="form-horizontal">
                     
                      <div className="form-group">
                                <label htmlFor="stat" className="col-sm-2 control-label">Status</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="status" disabled 
                                  value={finalStatus} id="stat" placeholder="Status"/>
                                </div>
                      </div>
                      
                      <div className="form-group">
                                <label htmlFor="action" className="col-sm-2 control-label">Last Action</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="status" disabled 
                                  value={new Date(parseInt(date)).toLocaleDateString()} id="action" placeholder="Status"/>
                                </div>
                      </div>
                      <div className="form-group">
                                <label htmlFor="course_name1" className="col-sm-2 control-label">Name</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="candidateName"  
                                  value={candidate.candidateName} id="course_name1" onChange={this.onChangeName.bind(this)} placeholder="candidate Name"/>
                                </div>
                      </div>
                      <div className="form-group">
                                <label htmlFor="mail" className="col-sm-2 control-label">Mail Id</label>
              
                                <div className="col-sm-9">
                                  <input type="mail" className="form-control"  id="mail" 
                                  value={candidate.candidateEmail} name="candidateEmail" disabled placeholder="Mail Id"/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="mob" className="col-sm-2 control-label">Mobile Number</label>
              
                                <div className="col-sm-9">
                                  <input type="tel" className="form-control"  id="mob"
                                  value={candidate.candidateContact} name="candidateContact" disabled placeholder="Mobile Number"/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="comp" className="col-sm-2 control-label">Company</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control"  id="comp" 
                                  value={candidate.work_experience[0].organization}
                                  onChange={this.onChangeCompany.bind(this)} name="company" placeholder="Company"/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="desg" className="col-sm-2 control-label">Designation</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control"
                                  value={candidate.work_experience[0].role} name="current_role" 
                                  onChange={this.onChangeDesignation.bind(this)}  id="desg" placeholder="Designation"/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="exp" className="col-sm-2 control-label">Experience</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="experience" 
                                  value={candidate.experience} id="exp" 
                                  onChange={this.onChangeExperience.bind(this)} placeholder="Experience"/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="ctc" className="col-sm-2 control-label">CTC</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="current_ctc" 
                                  value={candidate.current_ctc} id="ctc" onChange={this.onChangeCtc.bind(this)} placeholder="CTC"/>
                                </div>
                      </div> 
                      <div className="form-group">
                                <label htmlFor="et" className="col-sm-2 control-label">ET/EC</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="et" 
                                  value={candidate["et/ec"]} id="et" onChange={this.onChangeEt.bind(this)} placeholder="ET/EC"/>
                                </div>
                      </div>        
                      <div className="form-group">
                                <label htmlFor="np" className="col-sm-2 control-label">Notice Period</label>
              
                                <div className="col-sm-9">
                                  <input type="number" className="form-control" name="noticePeriod" 
                                  value={candidate.noticePeriod} id="np" min="0"
                                  onChange={this.onChangeNotice.bind(this)} placeholder="Notice Period"/>
                                </div>
                      </div>     
                      <div className="form-group">
                                <label htmlFor="univ" className="col-sm-2 control-label">University</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="college" 
                                  value={candidate.college} id="univ" onChange={this.onChangeUniversity.bind(this)} placeholder="University"/>
                                </div>
                      </div>      
                      <div className="form-group">
                                <label htmlFor="degree" className="col-sm-2 control-label">Degree</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" name="degree" 
                                  value={candidate.degree} id="degree" onChange={this.onChangeDegree.bind(this)} placeholder="Degree"/>
                                </div>
                      </div>
                      <div className="form-group">
                                <label htmlFor="location" className="col-sm-2 control-label">Location</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" id="location" 
                                  value={candidate.location} name="location" onChange={this.onChangeLocation.bind(this)} placeholder="Location"/>
                                </div>
                      </div>
                      {this.props.newKeys.map((value, index)=> {
                          return (
                            <div className="form-group" key={index}>
                                <label htmlFor={value} className="col-sm-2 control-label">{value}</label>
              
                                <div className="col-sm-9">
                                  <input type="text" className="form-control" id={value} 
                                  value={candidate[value]} name={value} onChange={(e)=>this.change(e,value)} placeholder={value}/>
                                </div>
                      </div>
                          )
                      })}
                                    
                    </form>
                        </div>
                    
                    <div className="modal-footer">
                     <button type="button" className="btn btn-primary" data-dismiss="modal" onClick={this.props.updateCandidate.bind(null,this.state.candidate)}>SAVE</button>
                      <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                    </div>
                  </div>
              
                </div>
        </div>
      </div>
    );
  }

}

export default CandidateModal;



