import React, { Component } from 'react';
import swal from 'sweetalert2';
import Select from 'react-select';
import Autosuggest  from 'react-autosuggest';
import Try from './try';
const $=window.$;
class JobsContainer extends Component {


    constructor(props){
        super(props);
        this.loading=true;
        this.index=0;
        this.active="all";
        this.JobsListCopy=[];
    }

    componentWillReceiveProps(nextprops){
       
    }


  render(){


    return (

        <div style={{backgroundColor:"#F5F5F5"}}>
       {/* <Try/> */}
       
       <div className="box-body container">
       <div className="container row" style={{margin:"10px auto"}}>
       <div className="col-xs-8">
       <nav id="job-tabs">
           <ul className="nav nav-pills">
               <li className={this.active=="all"?"active":""} onClick={()=>{
                   this.active="all";
                   this.props.resetJob();
                        }}><a>All</a></li>
               <li onClick={()=>{
                   this.active="invited";
                   this.props.filterByCreatedOrInvited(false)}} 
                   className={this.active=="invited"?"active":""}><a>Invited</a></li>
               <li onClick={()=>{
                   this.active="created";
                   this.props.filterByCreatedOrInvited(true)}} 
                   className={this.active=="created"?"active":""}><a>Created</a></li>
               <li onClick={()=>{
                   this.active="archived";
                   this.props.filterByArchive()}} 
               className={this.active=="archived"?"active":""}><a>Archived</a></li>
           </ul>
       </nav>
       </div>
                     
       <div className="text-right col-xs-4">
    <button className="btn btn-primary" data-toggle="modal" data-target="#addJob_modal">
    <i className="fa fa-plus"></i>Add Job</button>   
       </div>
      
       </div>
                            {/* <table className="table table-bordered table-hover table-striped">
                               <tbody>
                              {this.props.JobsList.length==0&&this.loading?
                              <tr> 
                                  <td colSpan="17" className="text-center" style={{fontSize:30,fontWeight:700}}>
                                  <span><i className="fa fa-spin fa-spinner"></i> LOADING</span></td></tr>
                                :null
                                }
                              </tbody>
                            </table>  */}
                            <div className="container">
                            { this.props.JobsList.map((value,index)=>
                                {  
                                return ( <div key={index} className="thumbnail row">
                                <div className="col-xs-9">     
                                    <h4 style={{color:"#696969"}}>{value.clientName} 
                                    <span className="pull-right">{ value.minExpense+' - '+value.maxExpense+' years'}
                                    </span> </h4>
                                    <h3 >{value.primarySkill + ' - '+ value.subLocation}</h3>
                                    <h5>{value.cityLocation}  
                                    {value.creatorID==localStorage.getItem("id")?<span className="pull-right label label-success">Created</span>
                                    :<span className="pull-right label label-warning">Invited</span>}
                                    </h5>
                               </div>
                               <div className="col-xs-3 text-center" style={{paddingTop:15}}>
                                <span>{value.isArchived?<a className="btn btn-default btn-lg" title="unarchive" onClick={()=>this.props.unArchiveJob(index)}>
                                <i className="fa fa-undo"></i></a> 
                                :<a className="btn btn-default btn-lg" title="archive" onClick={()=>this.props.archiveJob(index)}>
                                <i className="fa fa-archive"></i></a> }
                                </span> &nbsp;&nbsp;&nbsp;   
                                <a className="btn btn-info btn-lg" data-toggle="modal" data-target="#editJob_modal" 
                                onClick={()=>this.props.selectJob(index)}
                                ><i className="fa fa-pencil"></i></a>    
    
                               </div>
    
                                        </div>
                                    );
                                })
                                }
                        </div>
                    </div>
                          
                </div>
    );
  }


}

export default JobsContainer;



