import React, { Component } from 'react';
// import LevelOne from './LevelOne.js';
const $=window.$;

class Actions extends Component {

    constructor(props){
        super(props);
        this.state={
            subFilterIndex:0,
            finalFilterIndex:0
        };
    }

    render() {
        return (
            <div>
                <div className="box-header">
                    <h3 className="box-title" style={{marginTop:10}}>Candidates List</h3>
                    <span className="pull-right"><button className="btn btn-primary" data-toggle="modal" data-target="#excel_modal"><i className="fa fa-plus"></i>Download Excel</button></span>
                </div>

                <div className="row options">
                    <div className="col-xs-12 col-sm-6">JOB: &nbsp;
                    <select name="jobs" id="jobs" style={{"marginLeft":"10px"}} className="form-control" onChange={this.jobFilter.bind(this)}>
                            {this.props.JobRoles.map((value,index)=>{
                                return (
                                    <option value={value} key={index}>{value}</option>
                                );
                            })}
                        </select>
                    </div>
                </div>

                <div className="row options">
                    <div className="col-xs-12 col-sm-6">Filter By: &nbsp; &nbsp;
                        <select name="filter" id="filter" className="form-control" onChange={this.filterActivate.bind(this)}>
                            <option value="none">None</option>
                            <option value="status">Status</option>
                            <option value="recruiter">Recruiter</option>
                        </select>
                        {/* 1st Level Filter */}
                        <select name="recruiter" id="recruiter" style={{"marginLeft":"10px"}} className="form-control">
                            <option value="">-- Select Recruiter --</option>
                            {this.props.recuterNames.map((value,index)=>{
                                return (
                                    <option value={value} key={index}>{value}</option>
                                );
                            })}
                        </select>
                        <select name="status" id="status" style={{"marginLeft":"10px"}} className="form-control" onChange={this.selectSubFilter.bind(this)}>
                        <option value="">-- Select Primary Status --</option>
                            {this.props.statusList.map((status, index)=>{
                                return (
                                    <option value={status.value} key={index}>{status.optvalue}</option>
                                );
                            })}
                        </select>
                        
                        {/* 2nd level filter */}
                        <select name="substatus" id="substatus" style={{"marginLeft":"10px"}} className="form-control"  onChange={this.selectFinalFilter.bind(this)}>
                            {/* <option value=""> -- Select Sec Filter -- </option> */}
                            {this.props.subFilterList[this.state.subFilterIndex].apply.map((value,index)=>{
                                return (
                                    <option value={value.val} key={index}>{value.name}</option>
                                );
                            })}
                        </select>
                        {/* 3rd level filter */}
                        <select name="finalstatus" id="finalstatus" style={{"marginLeft":"10px"}} className="form-control">
                            <option value="">Select Round filter</option>
                            {this.props.finalFilterList[this.state.finalFilterIndex].value.map((value,index)=>{
                                return (
                                    <option value={value} key={index}>{value}</option>
                                );
                            })}
                        </select>
                    
                        <button className="btn btn-primary" onClick={this.manageFilter.bind(this)} style={{"marginLeft":"10px"}}>Set</button>
                    </div>
                    <div className="col-xs-12 col-sm-6 text-right">
                       <button className="btn btn-primary" style={{marginRight:10}}
                       data-toggle="modal" data-target="#add_modal"
                       ><i className="fa fa-plus"></i> Add Candidate</button>
                       
                        <input type="text" name="" id="search_field" className="form-control" placeholder="Search here"/>
                    </div>
                </div>
            </div>
        );
    }

    jobFilter() {
        this.props.filterByJob($("#jobs").val());
        // $("#filter").val("");
    }

    manageFilter(){
        let filter=$("#filter").val();        
        if(filter==="none")
            this.props.resetList();
        else if(filter==="status") {
            var status = $("#status").val();
            if($("#substatus").is(":visible") && $("#substatus").val() !== "") {
                status += ":"+ $("#substatus").val();
                if($("#finalstatus").is(":visible") && $("#finalstatus").val() !== "") {
                    status += ":"+ $("#finalstatus").val();
                }
            }
            this.props.filterByStatus(status);
        }
        else if(filter==="recruiter")
            this.props.filterByReruiter($("#recruiter").val());
    }

    componentDidMount(){
        $("#recruiter, #status, #substatus, #finalstatus").hide();
        $('#search_field').on('keyup',()=>{
            let patt = $("#search_field").val().toLowerCase();
            this.props.searchBy(patt);
        });
    }

    selectSubFilter(event) {
        let val = event.target.value;
        let index = this.props.subFilterList.findIndex((element)=>{
            return element.for === val;
        });
        if(index !== -1){
            this.setState({
                subFilterIndex: index
            });
            $("#substatus").show();
        }
        else
            $("#substatus").hide();
    }

    selectFinalFilter(event) {
        let val = event.target.value;
        let index = this.props.finalFilterList.findIndex(function(element) {
            return element.for === val;
        });
        if(index !== -1) {
            this.setState({
                finalFilterIndex: index
            });
            $("#finalstatus").show();
        }
        else
            $("#finalstatus").hide();
    }

    filterActivate(event) {
        let val=event.target.value;
        if(val==="none"){
        $("#recruiter, #status, #substatus, #finalstatus").hide();
            this.props.resetList();
        }
        else if(val==="status"){
                $("#recruiter").hide();
                $("#status").show();
        }
        else if(val==="recruiter"){
            $("#status, #substatus, #finalstatus").hide();
            $("#recruiter").show();
        }

    }

}

export default Actions;



